import React from 'react';

class DataComponent extends React.Component {
    renderCheckbox() {
      const handleOnChange = () => {
        this.props.update(this.props.name, !this.props.value ? 1 : 0);
      };
  
      return (
        <label class="checkbox_container">
            <input type="checkbox"
                id={this.props.name}
                name={this.props.name}
                checked={this.props.value || false}
                onChange={handleOnChange}/>
            {this.props.name}
        </label>
      );
    }
  
    renderTextbox() {
      const handleOnChange = (evt) => {
        const val = evt.target.value;
        this.props.update(this.props.name, val)
      };
  
      return (
        <div class="freeform_container">
          <label class="freeform_label">
            {this.props.name}
          </label>
          <input
            id={this.props.name}
            name={this.props.name}
            value={this.props.value || ""}
            class="freeform_textfield"
            onChange={handleOnChange} />
        </div>
      );
    }
  
    render() {
      switch (this.props.type) {
        case "boolean":
          return this.renderCheckbox();
        case "string":
          return this.renderTextbox();
        default:
          throw "Unknown data type: " + this.props.type;
      }
    }
  }

  export default DataComponent;