import React from 'react';
import './BuilderComponent.css';

import { saveAs } from 'file-saver';

class BuilderComponent extends React.Component {
    constructor(props) {
        super(props);
        this.navigate = props.navigate;
        this.state = {
            inputType: "boolean",
            fields: []
        };
    }

    getDefaultValue(inputType) {
        switch(inputType) {
            case "boolean":
                return 0;
            case "string":
                return "";
            default:
                return "";
        }
    } 

    addField() {
        this.setState({
            fields: [
                ...this.state.fields,
                {
                    inputType: this.state.inputType,
                    inputName: this.state.inputName,
                    inputValue: this.getDefaultValue(this.state.inputType),
                }
            ],
            inputName: "",
        });
    }

    updateInputName(evt) {
        const val = evt.target.value;
        this.setState({
            inputName: val
        });
    }

    updateType(evt) {
        const val = evt.target.value;
        this.setState({
            inputType: val
        });
    }

    removeField(i) {
        const newFields = this.state.fields;
        newFields.splice(i, 1);
        this.setState({fields: newFields});
    }

    continue() {
        this.navigate("./assistant", { state: {data: this.state.fields }});
    }

    saveConfig() {
        var blob = new Blob([JSON.stringify(this.state.fields)], {type: "data:text/json;charset=utf-8"});
        saveAs(blob, "coding-helper-config.json");
    }

    loadConfig(e) {
        const reader = new FileReader();
        reader.onload = (e) => {
            const text = e.target.result;
            this.setState({fields: JSON.parse(text)});
        };
        reader.readAsText(e.target.files[0]);
    }

    resetFilePicker(e) {
        const filePicker = document.getElementById("filePicker");
        filePicker.value = "";
        return e;
    }

    fields(fields) {
        const output = []
        for (const i in fields) {
            const field = fields[i];
            const fieldTypeName = (type) => {
                switch (type) {
                    case "boolean":
                        return "Checkbox";
                    case "string":
                        return "Text";
                    default:
                        return "Unknown";
                }
            };
            output.push(<tr>
                <td>{field.inputName}</td>
                <td class="row_center">{fieldTypeName(field.inputType)}</td>
                <td class="row_center">
                    <button onClick={() => this.removeField(i)}>
                        X
                    </button>
                </td>
              </tr>)
        }
        return output;
    }

    table() {
        return (
            <table class="table_fields">
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Type</th>
                        <th>Remove</th>
                    </tr>
                </thead>
                <tbody>
                    {this.fields(this.state.fields)}
                </tbody>
                <tfoot>
                    <tr>
                        <th class="name_cell">
                            <input
                                id="name"
                                name="name"
                                class="entry_name"
                                value={this.state.inputName}
                                onChange={evt => this.updateInputName(evt)}
                                placeholder="Name" />
                        </th>
                        <th>
                            <select
                                name="kind"
                                id="kind"
                                value={this.state.inputType}
                                onChange={evt => this.updateType(evt)}>
                                <option value="boolean">Checkbox</option>
                                <option value="string">Text</option>
                            </select>
                        </th>
                        <th>
                            <button
                                    disabled={!this.state.inputName}
                                    onClick={() => this.addField()}>
                                Add
                        </button>
                        </th>
                    </tr>
                </tfoot>
            </table>
        )
    }

    render() {
        return (
            <div class="builder_container">
                <span>Either load a configuration file from disk:</span>
                <input type="file" class="file_picker" id="filePicker" onClick={this.resetFilePicker} onChange={this.loadConfig.bind(this)} />
                <span>Or create your own configuration:</span>
                {this.table()}
                <div>
                    <div class="bottom_buttons">
                        <button
                            disabled={!this.state.fields.length > 0}
                            onClick={() => this.saveConfig()}>
                            Save to disk
                        </button>
                        <button
                            disabled={!this.state.fields.length > 0}
                            onClick={() => this.continue()}>
                            Continue
                        </button>
                    </div>
                </div>
            </div>
        )
    }
}

export default BuilderComponent;