import React from "react";
import * as ReactDOMServer from "react-dom/server";
import ReactDOM from "react-dom/client";
import "./index.css";
import HttpsRedirect from "react-https-redirect";
import { Navigate, HashRouter } from "react-router-dom";

import { BrowserRouter, Routes, Route } from "react-router-dom";
import App from "./App";
import WelcomePage from "./WelcomeComponent/WelcomePage";
import { MainPage } from "./MainComponent/MainComponent";
import AboutPage from "./AboutComponent/AboutPage";

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <HttpsRedirect>
    <HashRouter>
      <Routes>
        <Route path="/" element={<App />}>
          <Route path="assistant" element={<MainPage />} />
          <Route path="" element={<WelcomePage />} />
          <Route path="about" element={<AboutPage />} />
        </Route>
      </Routes>
    </HashRouter>
  </HttpsRedirect>
);
