import React, { useEffect } from 'react';

import {
  useNavigate,
  useLocation,
} from "react-router-dom";
import DataComponent from "../DataComponent/DataComponent";

export function MainPage() {
  const {state} = useLocation();
  const navigate = useNavigate();

  const isInitialized = state !== null;

  useEffect(() => {
    if (!isInitialized) {
      navigate("/");
    }
  });
  
  if (isInitialized) {
    return (<MainComponent state={state} />);
  }
}

/**
 * The main component of the app.
 */
class MainComponent extends React.Component {
  constructor(props) {
      super(props);
      this.state = props.state;
  }

  onResetClick() {
    var newData = this.state.data;
    newData = newData.map(item => {
      switch(item.inputType) {
        case "boolean":
          item.inputValue = 0;
          break;
        case "string":
          item.inputValue = "";
          break;
        default:
          break;
      }
      return item;
    });
    this.setState({
      data: newData
    });
  }
  
  onCopyClick() {
    const states = 
      Object.entries(this.state.data).map((([name, data], i) => {
        return data.inputValue;
      }));
      navigator.clipboard.writeText(states.join("\t"));
  }

  onUpdate(name, value) {
    var newData = this.state.data;
    newData = newData.map(item => {
      if (item.inputName === name) {
        item.inputValue = value;
      }
      return item;
    })
    this.setState({
      data: newData
    });
  }

  renderInputs() {
    const output = []
    for (const field of this.state.data) {
      output.push(
        <DataComponent
          type={field.inputType}
          name={field.inputName}
          value={field.inputValue}
          key={field.inputName}
          update={this.onUpdate.bind(this)}/>
      )
    }
    return output;
  }

  render() {
    return (
      <div class="app_container">
      <button
          onClick={() => this.onCopyClick()}>
        Copy to clipboard
      </button>
      {this.renderInputs()}
      <button
          class="reset_button"
          onClick={() => this.onResetClick()}>
        Reset
      </button>
    </div>
    );
  }
}